import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  routes: [{
      path: '/',
      name: 'index/:money',
      component: () =>
        import('@/views/home/index'),
      meta: {
        requireAuth: true
      },
      redirect: '/home'
    },
    {
      path: '/login',
      name: 'login',
      component: () =>
        import('@/views/login/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/complaint',
      name: 'complaint',
      component: () =>
        import('@/views/msg/complaint/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/notice/:id',
      name: 'notice',
      component: () =>
        import('@/views/msg/notice/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/news',
      name: 'news',
      component: () =>
        import('@/views/msg/news/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/home',  //
      name: 'home',
      component: () =>
        import('@/views/home/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/dataCenter', //
      name: 'dataCenter',
      component: () =>
        import('@/views/dataCenter/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/share',  //
      name: 'share',
      component: () =>
        import('@/views/share/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/message', //
      name: 'message',
      component: () =>
        import('@/views/message/index'),
      meta: {
        requireAuth: true
      }
    }, 
    {
      path: '/mine',  //
      name: 'mine',
      component: () =>
        import('@/views/mine/index'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/mall', //
      name: 'mall',
      component: () =>
        import('@/views/mall/mall_list')
    },
    {
      path: '/mall/mall_detail/:id', //
      name: 'mallDetail',
      component: () =>
        import('@/views/mall/mall_detail')
    },
    {
      path: '/mall/mall_pay/:id', //
      name: 'mallPay',
      component: () =>
        import('@/views/mall/mall_pay')
    },
    {
      path: '/mall/mall_address', //
      name: 'mallAddress',
      component: () =>
        import('@/views/mall/mall_address')
    },
    {
      path: '/mall/mall_addAddress/:id', //
      name: 'mallAddAddress',
      component: () =>
        import('@/views/mall/mall_addAddress')
    },
    {
      path: '/myOrder', //
      name: 'myOrder',
      component: () =>
        import('@/views/myOrder/myOrder')
    },
    {
      path: '/bindingMplements', //
      name: 'bindingMplements',
      component: () =>
        import('@/views/binding-mplements/index')
    },
    {
      path: '/myTerminal',
      name: 'myTerminal',
      component: () =>
        import('@/views/my-terminal/index')
    },
    {
      path: '/invitationRecord',
      name: 'invitationRecord',
      component: () =>
        import('@/views/invitation-record/index')
    },
    {
      path: '/invitationRecordDetailed/:id',
      name: 'invitationRecordDetailed',
      component: () =>
        import('@/views/invitation-record/invitation-record-detailed/index')
    },
    {
      path: '/deliverGoods',
      name: 'deliverGoods',
      component: () =>
        import('@/views/deliver-goods/index')
    },
    {
      path: '/myOrderDetail/:id', //
      name: 'myOrderDetail',
      component: () =>
        import('@/views/myOrder/myOrderDetail')
    },
    {
      path: '/financialDetails', //
      name: 'financialDetails',
      component: () =>
        import('@/views/financialDetails/financialDetails')
    },
    {
      path: '/withdrawal', //
      name: 'withdrawal',
      component: () =>
        import('@/views/withdrawal/withdrawal')
    }, 
    {
      path: '/withdrawalRecord', //
      name: 'withdrawalRecord',
      component: () =>
        import('@/views/withdrawal/withdrawalRecord')
    },
    { 
      path: '/clickDeliverGoods/:id', //
      name: 'clickDeliverGoods',
      component: () =>
        import('@/views/deliver-goods/click-deliver-goods/index')
    },
    {
      path: '/express', //
      name: 'express',
      component: () =>
        import('@/views/deliver-goods/express/index')
    },
    {
      path: '/msg-details/:id', //
      name: 'msgDetails',
      component: () =>
        import('@/views/msg/details/index')
    },
    {
      path: '/settings',  //
      name: 'settings',
      component: () =>
        import('@/views/settings/index')
    },
    {
      path: '/freezeprogress', //
      name: 'freezeprogress',
      component: () =>
        import('@/views/freezeprogress/index')
    }, 
    {
      path: '/myMerchants', //
      name: 'myMerchants',
      component: () =>
        import('@/views/myMerchants/index')
    }, 
    {
      path: '/about', //
      name: 'about',
      component: () =>
        import('@/views/about/index')
    }, 
    {
      path: '/changePhoneNumber', //
      name: 'changePhoneNumber',
      component: () =>
        import('@/views/changePhoneNumber/index')
    },
     {
      path: '/changePhoneResult', //
      name: 'changePhoneResult',
      component: () =>
        import('@/views/changePhoneNumber/changePhoneResult')
    }, 
    {
      path: '/changeCard', //
      name: 'changeCard',
      component: () =>
        import('@/views/changeCard/index')
    }, 
    {
      path: '/changePassword', //
      name: 'changePassword',
      component: () =>
        import('@/views/changePassword/index')
    }, 
    {
      path: '/feedback', //
      name: 'feedback',
      component: () =>
        import('@/views/feedback/index')
    },
    {
      path: '/certification', //--
      name: 'certification',
      component: () =>
        import('@/views/certification/index')
    }, 
    {
      path: '/certificationNext', //--
      name: 'certificationNext',
      component: () =>
        import('@/views/certification/certificationNext')
    },
    {
      path: '/organizationChart', //
      name: 'organizationChart',
      component: () =>
        import('@/views/organizationChart/index')
    },
    {
      path: '/certificationComplete', //
      name: 'certificationComplete',
      component: () =>
        import('@/views/certification/certificationComplete')
    },
    {
      path: '/affiliateTransactions', //
      name: 'affiliateTransactions',
      component: () =>
        import('@/views/affiliateTransactions')
    },
    {
      path: '/helpCenter', //
      name: 'helpCenter',
      component: () =>
        import('@/views/helpCenter')
    },
    {
      path: '/helpDetail/:id', //
      name: 'helpDetail',
      component: () =>
        import('@/views/helpCenter/helpDetail')
    },
    {
      path: '/ApplyAgent', //
      name: 'ApplyAgent',
      component: () =>
        import('@/views/ApplyAgent/index')
    }, 
    {
      path: '/organization', //
      name: 'organization',
      component: () =>
        import('@/views/organizationChart/organization')
    }, 
    {
      path: '/evaluation/:id', //
      name: 'evaluation',
      component: () =>
        import('@/views/evaluation')
    },
    {
      path: '/bindSuccess', //
      name: 'bindSuccess',
      component: () =>
        import('@/views/binding-mplements/bindSuccess')
    }
  ]
})



